import React, { FC } from 'react';
import dot from '../../../assets/homePage/timeline/dot.svg';
import lowershadow from '../../../assets/homePage/timeline/lowershadow.svg';
import uppershadow from '../../../assets/homePage/timeline/uppershadow.svg';
import { IEventInfo } from '../../../context/Interface';
import '../../../styles/home/TimeLine.scss';

type Props = {
    eventInfo: IEventInfo;
    last: boolean;
};
const TimeLine: FC<Props> = (props: Props) => {
    const { eventInfo, last } = props;
    return (
        <div className='timeLine-container'>
            <div className='timeLine-icon'>
                <img src={uppershadow} alt='Icon-uppershadow' />
                <img src={dot} alt='Icon-dot' id='timeline-dot' />
                {last ? (
                    <img src={uppershadow} alt='Icon-uppershadow' />
                ) : (
                    <div id='shadow-control'>
                        <img src={lowershadow} alt='Icon-lowershadow' id='timeline-lowershadow' />
                    </div>
                )}
            </div>
            <div className='timeLine-text'>
                <div className='event-date'>{eventInfo.date}</div>
                <div className='event-info'>{eventInfo.info}</div>
            </div>
        </div>
    );
};

export default TimeLine;
